import React from 'react';
import { pluginFactories } from '@react-page/plugins-slate';

export const getPaddingFromElement = (el: HTMLElement) => {
  const margin = el?.style?.padding;
  if (margin) {
    return {
      margin,
    };
  }
};

export default pluginFactories.createComponentPlugin<{
  margin: string;
}>({
  addHoverButton: false, // whether to show it above the text when selected
  addToolbarButton: false, // whether to show it in the bottom toolbar
  type: 'SetMargin', // a well defined string, this is kind of the id of the plugin
  label: 'Margin',
  object: 'block', // mark is like a span, other options are inline and block
  Component: 'p', // the component to render
  getStyle: ({ margin }) => ({ margin }),
  deserialize: {
    tagName: 'p',
    getData: getPaddingFromElement,
  },
});
