import slate from '@react-page/plugins-slate';
// import css as well. currently, we caannot do this here in the demo project and have moved that to _app.tsx
// see https://github.com/vercel/next.js/issues/19717
// import '@react-page/plugins-slate/lib/index.css';

import React from 'react';
import fontsizeSlatePlugin from './fontsizeSlatePlugin';
import fontweightSlatePlugin from './fontweightSlatePlugin';
import fontFamilySlatePlugin from './fontFamilySlatePlugin';
import lineHeightSlatePlugin from './lineHeightSlatePlugin';
import { ICON_NAMES, SVGIcon } from '@react-page/editor';
import fontcolorSlatePlugin from './fontcolorSlatePlugin';
import paddingSlatePlugin from './paddingSlatePlugin';
import marginSlatePlugin from './marginSlatePlugin';

export const headSlate = slate((def) => ({
  ...def,
  title: 'Heading',
  icon: ICON_NAMES.FONTS_BIG,

  plugins: {
    // this will pull in all predefined plugins
    ...def.plugins,
    // you can also add custom plugins. The namespace `custom` is just for organizing plugins
    custom: {
      fontcolor: fontcolorSlatePlugin,
      fontsize: fontsizeSlatePlugin,
      fontweight: fontweightSlatePlugin,
      fontfamily: fontFamilySlatePlugin,
      lineheight: lineHeightSlatePlugin,
      padding: paddingSlatePlugin,
      margin: marginSlatePlugin,
    },
  },
}));
