import React, { useEffect, useState } from 'react';
import Editor, { ButtonLoading } from '@react-page/editor';
import type { SiteInfoType } from '@react-page/editor';
import type { Value } from '@react-page/editor';
import { cellPlugins } from '../../../plugins/cellPlugins';
import { customSections } from '../../../plugins/customSections';
import { useRouter } from 'next/router';
import ScreenLoading from '../../../components/screenLoading';
import html2canvas from 'html2canvas';
import { toast } from 'react-toastify';
import type { NextPage } from 'next';
import EmptyPage from '../../../components/EmptyPage';
import Cookies from 'js-cookie';
import { IntercomProvider } from '../../../utils/IntercomProvider';
import { resizeBase64Image } from '../../../utils/imageFunc';
import { FooterRow } from '../../../sampleContents/FooterRow';

const LANGUAGES = [
  {
    lang: 'en',
    label: 'English',
  },
  {
    lang: 'de',
    label: 'Deutsch',
  },
];

const Builder: NextPage = () => {
  const router = useRouter();
  const [value, setValue] = useState<Value>({} as Value);
  const [siteInfoData, setSiteInfoData] = useState<SiteInfoType>(
    {} as SiteInfoType
  );
  const [confirmOpen, setConfirmOpen] = useState(false);
  const [loading, setLoading] = useState(false);
  const [pageLoad, setPageLoad] = useState(false);
  const [isNoPage, setIsNoPage] = useState(false);

  const { id: siteId } = router.query;

  useEffect(() => {
    if (siteId) {
      getSiteData();
    }
  }, [siteId]);

  const getSiteData = () => {
    const options = {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        authorization: 'bearer ' + Cookies.get('token'),
      },
    };

    try {
      fetch(
        `${process.env.NEXT_PUBLIC_SERVER_API_URL}/sites/${siteId}`,
        options
      )
        .then((res) => res.json())
        .then((data) => {
          if (data.code === 200) {
            setValue(data.data?.site.html_data);
            setSiteInfoData(data.data.site);
            setPageLoad(true);
          } else {
            setPageLoad(true);
            setIsNoPage(true);
          }
        });
    } catch (error) {
      toast.error('Network Error!');
    }
  };

  const saveSite = async (backColor: string) => {
    const element = document.getElementById('page-edit-view');
    if (element) {
      await html2canvas(element, { useCORS: true }).then(async (canvas) => {
        const ctx = canvas.getContext('2d');
        const imageData = ctx?.getImageData(0, 0, canvas.width, canvas.height);
        const croppedCanvas = document.createElement('canvas');
        croppedCanvas.width = 300;
        croppedCanvas.height = 200;
        const croppedCtx = croppedCanvas.getContext('2d');
        if (imageData) {
          croppedCtx?.drawImage(
            canvas,
            0,
            0,
            canvas.width,
            (canvas.width * 2) / 3,
            0,
            0,
            300,
            200
          );
        }
        const imgData = croppedCanvas.toDataURL();

        const data = {
          html_data: value || siteInfoData.html_data,
          image: imgData,
          html_background_color: backColor,
        };

        const options = {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
            authorization: 'bearer ' + Cookies.get('token'),
          },
          body: JSON.stringify(data),
        };

        try {
          await fetch(
            `${process.env.NEXT_PUBLIC_SERVER_API_URL}/sites/${siteId}`,
            options
          )
            .then((res) => res.json())
            .then((data) => {
              if (data.code === 200) {
                // console.log('Saved Success!');
              } else {
                toast.error(data.message);
              }
            });
        } catch (error) {
          toast.error('Network Error!');
        }
      });
    }
  };

  const saveSetting = async (siteName?: string, favicon?: string) => {
    type Org = Record<string, string>;
    const data: Org = {};

    data.name = siteName || siteInfoData.name;

    if (favicon !== siteInfoData.favicon) {
      const resizedBase64 = await resizeBase64Image(favicon || '', 50, 50);
      data.favicon = resizedBase64;
    }

    const options = {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
        authorization: 'bearer ' + Cookies.get('token'),
      },
      body: JSON.stringify(data),
    };

    try {
      await fetch(
        `${process.env.NEXT_PUBLIC_SERVER_API_URL}/sites/${siteId}`,
        options
      )
        .then((res) => res.json())
        .then((res) => {
          if (res.code === 200) {
            setSiteInfoData({
              ...siteInfoData,
              name: res.data.name,
              subdomain: res.data.subdomain,
              favicon: res.data.favicon,
            });
            toast.success('Saved Success!');
          } else {
            toast.error(res.message);
          }
        });
    } catch (error) {
      toast.error('Network Error!');
    }
  };

  const deleteSite = async () => {
    setConfirmOpen(true);
  };

  const handleDeleteSite = async () => {
    setLoading(true);
    const options = {
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json',
        authorization: 'bearer ' + Cookies.get('token'),
      },
    };

    try {
      await fetch(
        `${process.env.NEXT_PUBLIC_SERVER_API_URL}/sites/${siteId}`,
        options
      )
        .then((res) => res.json())
        .then((data) => {
          if (data.code === 200) {
            router.push({
              pathname: '/home',
            });
          } else {
            toast.error(data.message);
            setLoading(false);
          }
        });
    } catch (error) {
      toast.error('Network Error!');
      setLoading(false);
    }
  };

  const publishSite = async (published: boolean, backColor: string) => {
    const data = {
      html_data: value || siteInfoData.html_data,
      html_background_color: backColor,
      published: published,
    };
    const options = {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
        authorization: 'bearer ' + Cookies.get('token'),
      },
      body: JSON.stringify(data),
    };
    setLoading(true);
    try {
      await fetch(
        `${process.env.NEXT_PUBLIC_SERVER_API_URL}/sites/${siteId}/publish`,
        options
      )
        .then((res) => res.json())
        .then((data) => {
          if (data.code === 200) {
            toast.success('Publish Success!');
            window.open(siteInfoData.subdomain, '_blank');
          } else {
            toast.error(data.message);
          }
        });
    } catch (error) {
      toast.error('Network Error!');
    }
  };

  if (isNoPage === true) {
    return <EmptyPage />;
  }

  useEffect(() => {
    console.log(value);
  }, [value]);

  return (
    <>
      <IntercomProvider>
        <div className={pageLoad === true ? 'hidden' : 'block opacity-100'}>
          <ScreenLoading />
        </div>
        <Editor
          cellPlugins={cellPlugins}
          sections={customSections}
          value={value}
          lang={LANGUAGES[0].lang}
          onChange={setValue}
          languages={LANGUAGES}
          site={{
            siteInfoData: siteInfoData,
            saveSite: saveSite,
            saveSetting: saveSetting,
            publishSite: publishSite,
            deleteSite: deleteSite,
            // user: user,
          }}
        />
        <div id="dropdown-container"></div>
        <div id="modal-container"></div>
        <div
          className={
            'absolute flex top-0 left-0 w-[100vw] h-[100vh] bg-[#00000099] items-center justify-center z-20 backdrop-blur-[20px] ' +
            (confirmOpen === true ? '' : 'hidden')
          }
        >
          <div className="flex flex-col bg-[#181A20] gap-[24px] p-[24px] rounded-[12px] w-[360px] z-20">
            <div className="text-gray-2 text-[18px] leading-[28px] font-medium">
              Are you sure you want to delete it?
            </div>
            <div className="flex flex-row justify-between gap-[12px]">
              <button
                onClick={() => handleDeleteSite()}
                className="text-gray-2 bg-primary-blue h-[40px] rounded-[8px] px-5 w-full"
              >
                {loading ? <ButtonLoading /> : 'Confirm'}
              </button>
              <button
                onClick={() => setConfirmOpen(false)}
                className="text-gray-2 bg-alpha-10 h-[40px] rounded-[8px] px-5 w-full"
              >
                Cancel
              </button>
            </div>
          </div>
        </div>
      </IntercomProvider>
    </>
  );
};

export default Builder;
