import { ICON_NAMES, ModalHeader, toValidUrl } from '@react-page/editor';
import type { CellPlugin } from '@react-page/editor';
import React from 'react';
import ReactPlayer from 'react-player';
import { Input, Divider } from '@react-page/editor';

type Data = {
  src: string;
  height: string;
};
// you can pass the shape of the data as the generic type argument
const customSoundCloudPlugin: CellPlugin<Data> = {
  Renderer: ({ data }) => (
    <div
      style={{ display: 'flex', justifyContent: 'center', minHeight: '150px' }}
    >
      {data?.src && <ReactPlayer url={toValidUrl(data.src)} />}
    </div>
  ),
  id: 'sound-cloud',
  title: 'Soundcloud',
  icon: ICON_NAMES.SOUND_CLOUD,
  description: 'A sound cloud',
  version: 1,
  controls: {
    type: 'custom',
    Component: (props) => (
      <>
        <ModalHeader title="Embed Soundcloud Style" />

        <div style={{ fontSize: '14px', color: '#AAAAAA' }}>
          Insert Soundcloud song link
        </div>

        <Divider top={16} bottom={24} />

        <Input
          placeholder="https://"
          value={props.data.src ?? ''}
          onChange={(value, e) =>
            props.onChange({
              src: value,
            })
          }
        />
      </>
    ),
  },
};

export default customSoundCloudPlugin;
