import { ICON_NAMES, ModalHeader, toValidUrl } from '@react-page/editor';
import type { CellPlugin } from '@react-page/editor';
import React from 'react';
import { Input, Divider } from '@react-page/editor';
import { Spotify } from 'react-spotify-embed';

type Data = {
  src: string;
  height: string;
};
// you can pass the shape of the data as the generic type argument
const customSpotifyPlugin: CellPlugin<Data> = {
  Renderer: ({ data }) => (
    <div
      style={{ display: 'flex', justifyContent: 'center', minHeight: '150px' }}
    >
      {data?.src && <Spotify link={toValidUrl(data.src)} />}
    </div>
  ),
  id: 'spotify-song',
  title: 'Spotify',
  icon: ICON_NAMES.SPOTIFY,
  description: 'A spotify song',
  version: 1,
  controls: {
    type: 'custom',
    Component: (props) => (
      <>
        <ModalHeader title="Embed Spotify Style" />

        <div style={{ fontSize: '14px', color: '#AAAAAA' }}>
          Insert Spotify song link
        </div>

        <Divider top={16} bottom={24} />

        <Input
          placeholder="https://"
          value={props.data.src ?? ''}
          onChange={(value, e) =>
            props.onChange({
              src: value,
            })
          }
        />
      </>
    ),
  },
};

export default customSpotifyPlugin;
